@import url('//fonts.googleapis.com/css?family=Open+Sans:300,400,700');
@import url('//fonts.googleapis.com/css?family=Montserrat:400,700');

h1,h2,h3,h4,h5,h6{
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  color: #222;
}
h1{
  font-size: 2.8em;
}
h2{
  font-size: 1.7em;
  font-family: 'Montserrat';
  font-weight: bold;
}
h3{
  font-size: 1.8em;
}
h4{
  font-size: 1.5em;
}
h5{
  font-size: 1.05em;
  line-height: 1.6em;
  font-weight: bold;
  font-family: 'Montserrat'
}
h5 small{
  font-size: .9em;
  line-height: 1.6em;
}
h6{
  font-size: .9em;
}
.card-title h5{
  display: flex;
  justify-content: space-between;
  color: #222;
}

body {
  font-family: 'Open Sans', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.cardexpander.expanded{
  position: fixed;
  z-index: 50;

}
.cardexpander{
  width: 280px;
}



.gluten-free,
.vegan,
.featured{
  display: inline-flex;
  height: 22px;
  color: #fff;
  margin-right: 10px;
  border-radius: 11px;
  align-items: center;
  justify-content: center;
  padding: 0 8px;
  font-weight: 400;
  font-size: .9em
}
.gluten-free{
  background: #3cc68a;
}
.vegan{
  background: #90EE90;
}
.featured{
  background: 	#8FBC8F;
}
.card-title small .MuiFormControl-root {
 margin-left:20px;
}
.MuiTypography-root .MuiFormControl-root{
  width: 100%;
  margin-top:20px;
}
.MuiTypography-root{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.cardexpander .MuiTouchRipple-root{
  display: none;
}

.cardexpander .MuiCardActionArea-root.Mui-focusVisible .MuiCardActionArea-focusHighlight{
  opacity: 0;
}
.cardexpander .MuiPaper-root{
  width: auto;
  min-width: 250px;
}
.cardexpander.expanded .MuiPaper-root{
  box-shadow: 0px 25px 45px 0px rgba(0, 0, 0, 0.17);
}
.cardexpander .MuiPaper-root:hover{
  transform: none;
}
.cardexpander.expanded .MuiPaper-root .MuiCardMedia-root{
  height: 200px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.headerBG{

    position: absolute;
    background: rgba(0,0,0,.5);
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    content: "";
    z-index: 5;

}
.headerTitle{
  z-index: 6;
}
.headerTitle h1{
  margin-bottom:0;
}
.headerTitle h6{
  margin-top:10px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  overflow: hidden;
  margin-bottom: 50px;
}

.App-link {
  color: #61dafb;
}

body{
  min-height:5000px;
}

.container{
  max-width: 90%;
  margin: 0 auto 0 auto;
  padding: 30px 0 0 0;
  display: block;
  position: relative;
}

.form{
  padding: 0 15px;
}

h1.page-title{
  padding: 30px 0;
  border-bottom: 1px solid #e8e8e8;
}

h3.title{
  margin: 30px 0 10px 0;
  text-align: left;
  color: #333;
}

.add-to-menu{
  padding: 0 0 40px 0;
  display: flex;
}

.add-to-menu .form{
  min-width: 400px;
  text-align: left;
  width: 100%;
  display: flex;
}
.add-to-menu .form>div{
  width: 50%;
}
.add-to-menu .form .MuiFab-extended{
  display: inline-flex;
  margin: 0 10px;
}
.add-to-menu .form .cardexpander{
  width: auto!important;
}
.add-to-menu .form .MuiPaper-root{
  margin: 0 auto 20px auto;
  display: block;
  width: 350px;
  
}
.add-to-menu .form .MuiPaper-root .MuiButtonBase-root{
  cursor: default;
}
.add-to-menu .form .MuiCard-root:hover{
  transform: none;
  box-shadow: 0px 18px 37px 0px rgba(0, 0, 0, 0.07)
}
.add-to-menu .MuiFormControl-root{
  margin-top: 10px;
  display: block;
  width: 100%;
}
#demo-simple-select-label{


  background: #fff;
  z-index: 10;
}


.add-to-menu .MuiInputBase-root{
  width: 100%;
  border-radius: 0;
 
}
.add-to-menu .MuiCardContent-root{
  min-height: 113px;
}
body .MuiPaper-elevation8{
  margin-top:55px;
}
.add-to-menu .MuiInputBase-root:hover,
.add-to-menu .MuiInputBase-root:focus,
.add-to-menu .MuiInputBase-root.Mui-focused,
.add-to-menu .MuiSelect-select:focus{
  background:rgba(255,255,255,0);
}
.add-to-menu .select-section .MuiInputLabel{
  transform: translate(0, 20px) scale(1);
}
.add-to-menu .section-name{
  padding-right: 30px;
}
body .add-to-menu .section-position{
  width: 100px;
  padding-right: 30px;
}
#demo-simple-select-label.MuiInputLabel-shrink {
  transform: translate(0, -3px) scale(0.75);
  transform-origin: top left;
}

.add-to-menu .MuiCardActions-root{
  display: none;
}
.MuiButtonBase-root.MuiFab-root{
  box-shadow: none;
}
.MuiCardActions-root a
{
  font-size: .8rem;
  display: inline-flex;
  padding: 9px 10px;
  border-radius: 19px;
  color:#fff;
  
}
.MuiCardActions-root{
  position: absolute;
  margin-top:8px;
}
.MuiCardActions-root a:hover
{
  background:rgba(255,255,255,.4);
}
.MuiCardActions-root a svg{
  font-size: 1.3rem;
}

body{
  background:#fff;
}

.grid{
  display: flex;
  flex-wrap: wrap;
  margin: 20px auto 50px auto;

}
.grid-container{
  max-width: 90%;
  margin: 50px auto;
}

.MuiCardActionArea-root:hover .MuiCardActionArea-focusHighlight{
  opacity: 0!important;
}